// Table styles
// ---------------------

table.dataTable {
  // width: 100%;
  // margin: 0 auto;
  // clear: both;
  // border-collapse: separate;
  // border-spacing: 0;

  thead,
  tfoot {
    th {
      font-weight: bold;
    }
  }

  thead {
    th:active,
    td:active {
      outline: none;
    }
  }

  tfoot {
    th,
    td {
      padding: 10px 18px 6px 18px;
      border-top: 1px solid #111111;
    }
  }

  thead {
    .sorting,
    .sorting_asc,
    .sorting_desc     { cursor: pointer; }
    // .sorting          { background: url("@{images-path}sort_both.png") no-repeat center right; }
    // .sorting_asc      { background: url("@{images-path}sort_asc.png") no-repeat center right; }
    // .sorting_disabled { background: url("@{images-path}sort_asc_disabled.png") no-repeat center right; }
    // .sorting_desc     { background: url("@{images-path}sort_desc.png") no-repeat center right; }
    // .sorting_disabled { background: url("@{images-path}sort_desc_disabled.png") no-repeat center right; }
  }

  tbody {
    tr {
      background-color: white;

      &.selected {
        background-color: #b0bed9;
      }
    }

    th,
    td {
      padding: 8px @panel-body-padding;
    }
  }

  th.center,
  td.center,
  td.dataTables_empty {
    text-align: center;
  }

  th.right,
  td.right {
    text-align: right;
  }

  &.row-border,
  &.display {
    tbody {
      th,
      td {
        border-top: 1px solid @border-color-base;
      }

      tr:first-child {
        th,
        td {
          border-top: none;
        }
      }
    }
  }

  &.cell-border tbody {
    th,
    td {
      border-top: 1px solid @border-color-base;
      border-right: 1px solid @border-color-base;
    }

    tr {
      th:first-child,
      td:first-child {
        border-left: 1px solid @border-color-base;
      }
    }

    tr:first-child th,
    tr:first-child td {
      border-top: none;
    }
  }

  &.stripe,
  &.display {
    tbody tr.odd {
      background-color: #f9f9f9;
    }

    tbody tr.odd.selected {
      background-color: #abb9d3;
    }
  }

  &.hover,
  &.display {
    tbody {
      tr.odd:hover,
      tr.even:hover {
        background-color: whitesmoke;
      }

      tr.odd.selected,
      tr.even.selected {
        background-color: #a9b7d1;
      }
    }
  }

  &.order-column,
  &.display {
    tbody tr {
      > .sorting_1,
      > .sorting_2,
      > .sorting_3 { background-color: #f9f9f9; }
    }

    tbody tr.selected {
      > .sorting_1,
      > .sorting_2,
      > .sorting_3 { background-color: #acbad4; }
    }
  }

  &.order-column.stripe tbody,
  &.display tbody {
    tr.odd {
      > .sorting_1 { background-color: #f1f1f1; }
      > .sorting_2 { background-color: #f3f3f3; }
      > .sorting_3 { background-color: whitesmoke; }
    }

    tr.odd.selected {
      > .sorting_1 { background-color: #a6b3cd; }
      > .sorting_2 { background-color: #a7b5ce; }
      > .sorting_3 { background-color: #a9b6d0; }
    }

    tr.even {
      > .sorting_1 { background-color: #f9f9f9; }
      > .sorting_2 { background-color: #fbfbfb; }
      > .sorting_3 { background-color: #fdfdfd; }
    }

    tr.even.selected {
      > .sorting_1 { background-color: #acbad4; }
      > .sorting_2 { background-color: #adbbd6; }
      > .sorting_3 { background-color: #afbdd8; }
    }
  }

  &.order-column.hover tbody,
  &.display tbody {
    tr:hover,
    tr.odd:hover,
    tr.even:hover {
      > .sorting_1 { background-color: #eaeaea; }
      > .sorting_2 { background-color: #ebebeb; }
      > .sorting_3 { background-color: #eeeeee; }
    }

    tr:hover.selected,
    tr.odd:hover.selected,
    tr.even:hover .selected {
      > .sorting_1 { background-color: #a1aec7; }
      > .sorting_2 { background-color: #a2afc8; }
      > .sorting_3 { background-color: #a4b2cb; }
    }
  }

  &.no-footer {
    border-bottom: 0;
  }

  &,
  th,
  td {
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
  }
}

// Control feature layout
// -----------------------------
.dataTables_wrapper {
  // position: relative;
  // clear: both;
  // zoom: 1;

  .dataTables_length,
  .dataTables_filter,
  .dataTables_info,
  .dataTables_processing,
  .dataTables_paginate {
    color: inherit;
  }

  .dataTables_length,
  .dataTables_filter {
    width: 50%;
  }

  .dataTables_length {
    float: left;
  }

  .dataTables_filter {
    float: right;
    text-align: right;

    input {
      margin-left: 0.5em;
    }
  }

  .dataTables_paginate,
  .dataTables_info {
    padding-top: 0;
  }

  .dataTables_info {
    clear: both;
    float: left;
    margin: 10px 0;
  }

  .dataTables_paginate {
    float: right;
    text-align: right;

    .paginate_button {
      min-width: 0;
      padding: 0;
      margin: 0;
      color: inherit;
      // cursor: pointer;
      border: 0;

      &.current,
      &.current:hover {
        // color: #333333 !important;
        border: 1px solid #cacaca;
        background-color: white;
        background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, white), color-stop(100%, gainsboro));
        // Chrome,Safari4+
        background: -webkit-linear-gradient(top, white 0%, gainsboro 100%);
        // Chrome10+,Safari5.1+
        background: -moz-linear-gradient(top, white 0%, gainsboro 100%);
        // FF3.6+
        background: -ms-linear-gradient(top, white 0%, gainsboro 100%);
        // IE10+
        background: -o-linear-gradient(top, white 0%, gainsboro 100%);
        // Opera 11.10+
        background: linear-gradient(to bottom, white 0%, gainsboro 100%);
        // W3C
      }

      &:hover,
      &.disabled,
      &.disabled:hover,
      &.disabled:active {
        cursor: default;
        color: inherit !important;
        border: 0;
        background: transparent;
        box-shadow: none;
      }

      // &:hover {
      //   color: white !important;
      //   background-color: #585858;
      //   background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #585858), color-stop(100%, #111111));
      //   // Chrome,Safari4+
      //   background: -webkit-linear-gradient(top, #585858 0%, #111111 100%);
      //   // Chrome10+,Safari5.1+
      //   background: -moz-linear-gradient(top, #585858 0%, #111111 100%);
      //   // FF3.6+
      //   background: -ms-linear-gradient(top, #585858 0%, #111111 100%);
      //   // IE10+
      //   background: -o-linear-gradient(top, #585858 0%, #111111 100%);
      //   // Opera 11.10+
      //   background: linear-gradient(to bottom, #585858 0%, #111111 100%);
      //   // W3C
      // }

      // &:active {
      //   outline: none;
      //   background-color: #2b2b2b;
      //   background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #2b2b2b), color-stop(100%, #0c0c0c));
      //   // Chrome,Safari4+
      //   background: -webkit-linear-gradient(top, #2b2b2b 0%, #0c0c0c 100%);
      //   // Chrome10+,Safari5.1+
      //   background: -moz-linear-gradient(top, #2b2b2b 0%, #0c0c0c 100%);
      //   // FF3.6+
      //   background: -ms-linear-gradient(top, #2b2b2b 0%, #0c0c0c 100%);
      //   // IE10+
      //   background: -o-linear-gradient(top, #2b2b2b 0%, #0c0c0c 100%);
      //   // Opera 11.10+
      //   background: linear-gradient(to bottom, #2b2b2b 0%, #0c0c0c 100%);
      //   // W3C
      //   box-shadow: inset 0 0 3px #111;
      // }
    }
  }

  .dataTables_processing {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 40px;
    margin-left: -50%;
    margin-top: -25px;
    padding-top: 20px;
    text-align: center;
    font-size: 1.2em;
    // background-color: white;
    // background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba(255, 255, 255, 0)), color-stop(25%, rgba(255, 255, 255, 0.9)), color-stop(75%, rgba(255, 255, 255, 0.9)), color-stop(100%, rgba(255, 255, 255, 0)));
    // // Chrome,Safari4+
    // background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 25%, rgba(255, 255, 255, 0.9) 75%, rgba(255, 255, 255, 0) 100%);
    // // Chrome10+,Safari5.1+
    // background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 25%, rgba(255, 255, 255, 0.9) 75%, rgba(255, 255, 255, 0) 100%);
    // // FF3.6+
    // background: -ms-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 25%, rgba(255, 255, 255, 0.9) 75%, rgba(255, 255, 255, 0) 100%);
    // // IE10+
    // background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 25%, rgba(255, 255, 255, 0.9) 75%, rgba(255, 255, 255, 0) 100%);
    // // Opera 11.10+
    // background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 25%, rgba(255, 255, 255, 0.9) 75%, rgba(255, 255, 255, 0) 100%);
    // // W3C
  }

  .dataTables_scroll {
    clear: both;

    .dataTables_scrollBody {
      -webkit-overflow-scrolling: touch;

      th,
      td {
        > .dataTables_sizing {
          height: 0;
          overflow: hidden;
          margin: 0 !important;
          padding: 0 !important;
        }
      }
    }
  }

  &.no-footer {
    // .dataTables_scrollBody {
    //   border-bottom: t;
    // }

    .dataTables_scrollHead,
    .dataTables_scrollBody {
      table {
        border-bottom: none;
      }
    }
  }

  &:after {
    visibility: hidden;
    display: block;
    content: "";
    clear: both;
    height: 0;
  }
}

.dt-header,
.dt-footer {
  label {
    margin-bottom: 0;
  }
}

.dt-header {
  &:extend(.clearfix all);
  background: @panel-footer-bg;
  padding: @panel-body-padding;
  border-bottom: 1px solid #ddd;
}

.dt-footer {
  &:extend(.panel-footer all);
}

